#builder {
    > .global--header,
    > .component--uswds-banner,
    > .navigation--primary,
    > .global--footer {
        font-size: 16px;
    }
    
    > .global--header {
        padding: 2rem;
        height: auto;
        .global--header__version {
            color: $type-body;
        }
        .text--product-identity {
            &__top {
                font-size: 1.6666rem;
            }
        }
        .component--branding {
            height: 4.6666666667rem;
        }
    }
    > .navigation--primary {
        .navigation--primary__inner {
            &__item {
                a,
                button {
                    font-size: 1.25rem;
                    padding: 1rem 2.6666666667rem;
                }
            }
        }
    }
    > .main {
        > .layouts--main {
            margin: 0 !important;
            .global--header {
                .component--branding__vertical-bar {
                    display: none;
                }
            }
            .navigation--mobile-rail__button {
                background: $white;
                border-bottom: $s-1px solid $white;
                &:hover,
                &:focus {
                    background: $color-theme-1-1-1;
                    border-bottom: $s-1px solid $color-theme-1-1-1;
                }
            }
        }
    }
    > .global--footer {
        .global--footer__content {
            &__list {
                margin-top: 1.333333rem;
                margin-bottom: 1.333333rem;
                li {
                    margin-bottom: 1.25rem;
                }
            }
        }
        .global--footer__tagline {
            color: $type-body;
        }
    }
    .layouts--tabs {
        .layouts--body {
            margin: 0;
        }
    }
    .component--modal__wrapper__content__header__close {
        height: 4rem;
        width: 4rem;
        right: -2rem;
        top: -2rem;
    }
}